/*-------- Pricing Tables -------*/

.pricing {
	border-radius: 20px;
	box-shadow: 0 10px 40px 0 rgba(62, 57, 107, 0.1), 0 2px 9px 0 rgba(62, 57, 107, 0.1);
}
.pricing1 {
	color: #1f252d;
	border-radius: 7px;
}
.pricing .card-category {
	padding: 10px 0;
	border-radius: 4px;
	background: $border-color;
	color: #fff;
}
.horizontal-line {
	margin-top: 1rem;
	margin-bottom: 1rem;
	width: 80%;
}
.pricing {
	.btn {
		color: #fff !important;
		border-width: 5px !important;
		border-radius: 100px;
		font-size: 16px;
		font-weight: 700 !important;
	}
	.price {
		font-weight: 600;
		font-size: 50px;
		color: #434258 !important;
	}
	.list-unstyled li {
		border-bottom: 1px solid rgba(232, 235, 243, 0.7);
		padding: 7px 0;
	}
}
.pricing1 {
	.list-unstyled li {
		border-bottom: 1px solid $border-color;
		padding: 7px 0;
	}
	.card-category {
		background: rgba(255, 255, 255, 0.3);
		padding: 20px 0;
		color: #fff;
	}
}
.pricing-table {
	width: 100%;
	margin: 0 auto;
	transition: all .3s ease;
	-o-transition: all .3s ease;
	-moz-transition: all .3s ease;
	-webkit-transition: all .3s ease;
	border-radius: 0;
	&:hover {
		box-shadow: 0 0 30px rgba(49, 45, 101, 0.2);
		> {
			.panel-footer-landing {
				box-shadow: 0 0 30px rgba(49, 45, 101, 0.05) inset;
				-webkit-transition: all .3s ease;
			}
			.panel> {
				.panel-body-landing {
					background: #eef2f9;
					-webkit-transition: all .3s ease;
				}
				.panel-heading-landing-box {
					background: #fff !important;
					color: $text-color !important;
					-webkit-transition: all .3s ease;
				}
				.controle-header {
					background: #eef2f9 !important;
					-webkit-transition: all .3s ease;
				}
				.panel-footer {
					background: #eef2f9 !important;
					-webkit-transition: all .3s ease;
					text-align: center;
					>.btn {
						border: solid 1px #fff !important;
						-webkit-transition: all .3s ease;
					}
				}
			}
		}
	}
}
.btn-price:hover {
	background: #fff !important;
	color: #eef2f9 !important;
	-webkit-transition: all .3s ease;
}
.pricing-table {
	&:hover {
		>.panel>.controle-header>.panel-title-landing {
			color: #fff !important;
			-webkit-transition: all .3s ease;
			font-size: 35px;
		}
		>.panel>.panel-body-landing>.table>tbody>tr>td {
			font-weight: 500;
			-webkit-transition: all .3s ease;
		}
	}
	>.panel>.controle-header>.panel-title-landing {
		color: #000 !important;
		font-size: 35px;
	}
}
.table> {
	tbody>tr>th {
		font-weight: 500;
		-webkit-transition: all .3s ease;
	}
	tfoot>tr> {
		td, th {
			font-weight: 500;
			-webkit-transition: all .3s ease;
		}
	}
	thead>tr> {
		td, th {
			font-weight: 500;
			-webkit-transition: all .3s ease;
		}
	}
}
.panel-body-landing:hover>.table>tbody>tr>td {
	color: #fff !important;
	-webkit-transition: all .3s ease;
}
.panel-heading-landing {
	background: #f7f7f7 !important;
	padding: 20px !important;
	border-top-left-radius: 10px !important;
	border-top-right-radius: 10px !important;
	border: solid 2px #eef2f9 !important;
	border-bottom: none !important;
	text-align: center;
	margin-top: 20px;
}
.col-sm-4 .pricing-table .table td {
	padding: .75rem;
	vertical-align: top;
	text-align: left;
}
.panel-heading-landing-box {
	background: #eef2f9 !important;
	color: #fff !important;
	font-size: 16px !important;
	text-align: center;
	border-top: none !important;
}
.panel-title-landing {
	color: #eef2f9 !important;
	font-size: 35px;
	font-weight: 700;
}
.panel-body-landing {
	border: solid 2px #eef2f9 !important;
	border-top: none !important;
	border-bottom: none !important;
	text-align: center;
	background: #fff;
}
.panel-footer-landing {
	border: solid 2px #eef2f9 !important;
	border-bottom-left-radius: 10px !important;
	border-bottom-right-radius: 10px !important;
	border-top: none !important;
	text-align: center;
	background: #f7f7f7;
}
.panel.price {
	-moz-transition: all .3s ease;
	-o-transition: all .3s ease;
	-webkit-transition: all .3s ease;
	>.panel-heading {
		-moz-transition: all .3s ease;
		-o-transition: all .3s ease;
		-webkit-transition: all .3s ease;
	}
	h3 {
		margin-bottom: 0;
		padding: 20px 0;
	}
}
.panel-heading {
	padding: 10px 15px;
	background: #fff;
}
.panel.price>.panel-heading {
	padding: 0;
	position: relative;
}
.price .list-group-item {
	border-bottom: 1px solid rgba(250, 250, 250, 0.5);
}
.panel.price {
	.list-group-item {
		&:last-child {
			border-bottom-right-radius: 0;
			border-bottom-left-radius: 0;
		}
		&:first-child {
			border-top-right-radius: 0;
			border-top-left-radius: 0;
		}
	}
	margin-bottom: 1.5rem;
}
.price {
	.panel-footer {
		border-bottom: 0;
		background-color: #fff;
	}
	&.panel-color>.panel-body {
		background-color: #fff;
	}
}
.panel-body {
	padding: 15px;
	.lead {
		strong {
			font-size: 40px;
			margin-bottom: 0;
			font-weight: 400;
		}
		font-size: 20px;
		margin-bottom: 0;
		padding: 10px 0;
	}
}
.panel-footer {
	padding: 10px 15px;
	background-color: #f7f7f7;
	border-top: 1px solid $border-color;
	border-bottom-right-radius: 3px;
	border-bottom-left-radius: 3px;
}
.panel.price .btn {
	border: 0;
}
.heading-inverse {
	background-color: $text-color;
	color: #fff;
	padding: 5px;
}
.heading-secondary {
	color: #fff;
	padding: 5px;
}
.heading-success {
	background-color: #3eb750;
	color: #fff;
	padding: 5px;
}
.heading-info {
	background-color: #23bcb7;
	color: #fff;
	padding: 5px;
}
.heading-warning {
	background-color: #ffb609;
	color: #fff;
	padding: 5px;
}
.heading-danger {
	background-color: #fe6567;
	color: #fff;
	padding: 5px;
}
.pricingTable {
	padding: 25px 0;
	border: 1px solid $border-color;
	text-align: center;
	.price-value {
		font-size: 50px;
		font-weight: 800;
		color: #0d3b66;
		letter-spacing: 3px;
		margin-bottom: 30px;
	}
	.month {
		display: block;
		font-size: 17px;
		font-weight: 400;
		color: #0d3b66;
		letter-spacing: 2px;
		line-height: 10px;
		text-transform: uppercase;
	}
	.title {
		display: inline-block;
		padding: 15px 40px;
		margin: 0 0 20px 0;
		font-size: 20px;
		text-transform: capitalize;
		letter-spacing: 1px;
		position: relative;
		transition: all .3s ease 0s;
		&:after, &:before {
			content: "";
			border-right: 26px solid rgba(233, 30, 99, 0.1);
			border-top: 26px solid transparent;
			border-bottom: 26px solid transparent;
			position: absolute;
			top: 0;
			left: -26px;
			transition: all .3s ease 0s;
		}
		&:after {
			border-right: none;
			border-left: 26px solid rgba(233, 30, 99, 0.1);
			left: auto;
			right: -26px;
		}
	}
	.pricing-content {
		padding: 0;
		margin: 0 0 25px 0;
		list-style: none;
		li {
			font-size: 17px;
			line-height: 40px;
		}
	}
	.pricingTable-signup {
		display: inline-block;
		padding: 7px 30px;
		font-size: 17px;
		font-weight: 700;
		text-transform: uppercase;
		position: relative;
		transition: all .3s ease 0s;
		&:after, &:before {
			content: "";
			position: absolute;
			top: 0;
			left: 0;
			bottom: 0;
			right: 0;
			transition: all .5s ease 0s;
		}
		&:hover {
			&:before {
				background: rgba(255, 255, 255, 0.7);
				left: 60%;
				right: 60%;
				z-index: 1;
			}
			&:after {
				background: rgba(255, 255, 255, 0.7);
				top: 60%;
				bottom: 60%;
			}
		}
	}
}
.pricingTable2 {
	text-align: center;
	transition: all .4s ease-in-out 0s;
	background: #fff;
	border: 1px solid $border-color;
	border-radius: 5px;
	overflow: hidden;
}
.pricingTable2-header {
	padding: 25px 10px;
	background-color: #fff;
}
.pricingTable2 .pricingTable2-header h3 {
	font-size: 20px;
	letter-spacing: 2px;
	color: #000;
	text-transform: capitalize;
	margin: 0px;
	font-weight: 600;
}
.pricingTable2-header span {
	display: none;
	font-size: 12px;
}
.pricing-plans {
	padding: 25px 0;
}
.price-value1 {
	font-size: 25px;
	line-height: 35px;
	color: #fff;
	display: inline-block;
	position: relative;
	i {
		position: absolute;
		top: 3px;
		font-size: 22px;
	}
	span {
		font-size: 40px;
		color: #fff;
		margin-left: 17px;
		font-weight: 700;
	}
}
.pricing-plans .month {
	color: #fff;
	display: inline-block;
	font-size: 15px;
	margin-top: 15px;
	font-weight: 100;
}
.pricingContent2 ul {
	padding: 0 15px;
	margin-bottom: 0;
	list-style: none;
	li {
		padding: 25px 0 0 0;
		text-transform: capitalize;
		&:last-child {
			padding-bottom: 25px;
		}
	}
}
.price-value-content ul li {
	padding: 8px 0;
	text-transform: capitalize;
}
.pricingTable2 {
	.pricingTable2-sign-up {
		padding: 25px 0;
		border-top: 1px solid $border-color;
	}
	.btn-block {
		width: 50%;
		margin: 0 auto;
		color: #fff;
		text-transform: uppercase;
		border-radius: 50px;
		transition: .3s ease;
	}
	&.green .pricing-plans {
		background-color: #3eb750;
	}
	&.info .pricing-plans {
		background-color: #23bcb9;
	}
}
/*-------- Pricing Tables -------*/
