/*-------- Breadcrumb Styles -------*/

.breadcrumb {
	display: flex;
	flex-wrap: wrap;
	margin-bottom: 0 !important;
	list-style: none;
	border-radius: 5px;
	left: 0;
	bottom: 0;
	padding: 0 !important;
	background-color: transparent !important;
}
.breadcrumb-item a {
	color: rgba(255, 255, 255, 0.8);
}
.page-header .breadcrumb-item+.breadcrumb-item::before {
	color: #3f3e50;
	opacity: 0.3;
}
.breadcrumb-item+.breadcrumb-item {
	padding-left: 0;
	&::before {
		display: inline-block;
		padding-right: .5rem;
		padding-left: .5rem;
		color: rgba(255, 255, 255, 0.3);
		content: "/";
	}
	&:hover::before {
		text-decoration: underline;
		text-decoration: none;
	}
}
.breadcrumb1 {
	display: flex;
	flex-wrap: wrap;
	padding: .75rem 1rem;
	margin-bottom: 1rem;
	list-style: none;
	background-color: $border-color;
	border-radius: .25rem;
}
.breadcrumb-item1 {
	a {
		color: #7b838f;
		&:hover {
			text-decoration: underline;
		}
	}
	+.breadcrumb-item1 {
		&::before {
			display: inline-block;
			padding-right: .5rem;
			padding-left: .5rem;
			content: "/";
		}
		&:hover::before {
			text-decoration: underline;
			text-decoration: none;
		}
	}
	&.active {
		color: #b3b1cc;
	}
}
.breadcrumb-1, .breadcrumb-2 {
	ol {
		position: relative;
		display: block;
		width: 100%;
		text-align: right;
	}
	li {
		text-align: right;
		display: contents;
	}
}
.breadcrumb-3, .breadcrumb-4 {
	ol {
		position: relative;
		display: block;
		width: 100%;
		text-align: center;
	}
	li {
		display: contents;
		text-align: center;
	}
}
.breadcrumb-3 li, ::-ms-backdrop, .breadcrumb-4 li, ::-ms-backdrop, .breadcrumb-1 li, ::-ms-backdrop, .breadcrumb-2 li, ::-ms-backdrop {
	display: inline-block;
	text-align: center;
}
/*-------- Breadcrumb Styles -------*/
